      }

      var startStr =
        startYStr + "-" + startMStr + "-" + startDStr + " " + startTime + ":00";
      var endStr =
        startYStr + "-" + startMStr + "-" + startDStr + " " + endTime + ":00";
      var diasPtBr = ["Dom.", "Seg.", "Ter.", "Qua.", "Qui.", "Sex.", "Sab."];
      var day = diasPtBr[startDate.getDay()];
      var niceDateShort =
        day + ", " + startDStr + "/" + startMStr + "/" + startYStrShort;
