<template>
  <v-container>
    <div class="d-flex flex-column">
      <v-card
        class="pa-5 my-1"
        tile
        style="background-color: rgba(197, 209, 63, 0.6)"
      >
        <v-row class="mx-4">
          <v-text-field
            v-model="examP.nome"
            solo
            label="Ttulo"
            @change="onUpdate"
            clearable
          ></v-text-field>
        </v-row>
        <v-row class="mx-4">
          <v-textarea
            v-model="examP.examText"
            solo
            label="Exames"
            height="250"
            @change="onUpdate"
            clearable
          ></v-textarea>
        </v-row>
        <v-row class="mx-4">
          <v-textarea
            v-model="examP.observacoes"
            solo
            label="Observaes"
            height="100"
            @change="onUpdate"
            clearable
          ></v-textarea>
        </v-row>
      </v-card>
    </div>
  </v-container>
</template>

<script>
import { mdiClose, mdiPlus, mdiAutorenew } from "@mdi/js";

export default {
  data: function () {
    return {
      icons: { mdiClose, mdiPlus, mdiAutorenew },
      examP: {
        nome: "",
        examText: "",
        observacoes: "",
      },
    };
  },
  computed: {},
  methods: {
    onUpdate() {
      this.$store.dispatch("firestore/examPUpdate", {
        examP: this.examP,
      });
    },
  },
  mounted() {},
};
</script>