<template>
  <v-navigation-drawer
    v-model="drawer"
    class="grey lighten-5"
    :expand-on-hover="expandOnHover"
    :mini-variant="miniVariant"
    :right="right"
    :permanent="permanent"
    v-if="userInfos !== null"
  >
    <v-list-item>
      <v-list-item-avatar
        width="100%"
        height="100%"
        class="pr-2"
      >
        <img
          :src="userInfos.imagem"
          aspect-ratio="1"
        />
      </v-list-item-avatar>
    </v-list-item>

    <v-list-item
      two-line
      :class="miniVariant && 'px-0'"
    >
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <v-chip
            v-bind="attrs"
            v-on="on"
            to="/PacientePersonalData"
          >
            <v-icon left>{{ icons.mdiAccountDetailsOutline }}</v-icon>
          </v-chip>
        </template>
        <span>Editar dados pessoais</span>
      </v-tooltip>
      <v-list-item-content>
        <v-list-item-title>{{ userInfos.nome }}</v-list-item-title>
        <!-- <v-list-item-subtitle>{{
            userInfos.nomeClinica
          }}</v-list-item-subtitle> -->
      </v-list-item-content>
    </v-list-item>

    <v-list
      dense
      nav
      class="py-0"
    >
      <v-divider></v-divider>

      <v-list-item
        v-for="item in items"
        :key="item.title"
        link
      >
        <v-menu
          offset-x
          open-on-hover
        >
          <template v-slot:activator="{ on, attrs }">
            <v-row
              v-bind="attrs"
              v-on="on"
            >
              <v-list-item-icon>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </v-row>
          </template>
          <v-list>
            <v-banner
              color="grey lighten-3"
              single-line
            >
              <v-avatar
                slot="icon"
                color="#c5d13f"
                size="35"
              >
                <v-icon size="80%">{{ item.icon }}</v-icon>
              </v-avatar>
              {{ item.title }}
              <template v-slot:actions>
                <v-btn icon>
                  <v-icon>{{ icons.mdiClose }}</v-icon>
                </v-btn>
              </template>
            </v-banner>
            <v-divider></v-divider>
            <v-list-item
              v-for="(subitem, index) in item.subitems"
              :key="index"
            >
              <router-link
                :to="{ name: subitem.route }"
                style="text-decoration: none; color: inherit"
                v-slot="{ href, navigate, isActive, isExactActive }"
                replace
              >
                <v-list-item
                  :class="[
                      isActive && 'router-link-active',
                      isExactActive && 'router-link-exact-active',
                    ]"
                  :href="href"
                  @click="navigate"
                >
                  <v-list-item-icon>
                    <v-icon>{{ subitem.icon }}</v-icon>
                  </v-list-item-icon>
                  <v-list-item-title>{{ subitem.title }}</v-list-item-title>
                </v-list-item>
              </router-link>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
  <v-navigation-drawer v-else>
  </v-navigation-drawer>

</template>

<script>
import {
  mdiFinance,
  mdiWeightKilogram,
  mdiHuman,
  mdiTextBox,
  mdiTextBoxOutline,
  mdiFoodApple,
  mdiNeedle,
  mdiTelevision,
  mdiTelevisionPlay,
  mdiCalendarStar,
  mdiGift,
  mdiCartVariant,
  mdiClose,
  mdiAccountDetailsOutline,
  mdiCamera,
  mdiFileQuestionOutline
} from "@mdi/js";

import firebase from "firebase";

export default {
  data () {
    return {
      userInfos: null,
      drawer: null,
      items: [
        {
          title: "Prescies",
          icon: mdiTextBox,
          subitems: [
            {
              title: "Dietas",
              icon: mdiFoodApple,
              route: "PacienteDietas",
            },
            {
              title: "Frmulas",
              icon: mdiTextBoxOutline,
              route: "PacienteFormulas",
            },
            {
              title: "Exames Laboratoriais",
              icon: mdiNeedle,
              route: "PacienteExames",
            },
          ],
        },        
        {
          title: "Dados",
          icon: mdiFinance,
          subitems: [
            {
              title: "Minha evoluo (Peso, Medidas etc)",
              icon: mdiWeightKilogram,
              route: "PacienteEvolucao",
            },
            {
              title: "Bioimpedncias",
              icon: mdiHuman,
              route: "PacienteBioimpedancias",
            },
            {
              title: "Fotos",
              icon: mdiCamera,
              route: "PacienteFotos",
            },
            {
              title: "Questionrio (Pr-Atendimento)",
              icon: mdiFileQuestionOutline,
              route: "PacientePreAtendimento",
            },
          ],
        },
        {
          title: "Aulas",
          icon: mdiTelevisionPlay,
          subitems: [
            {
              title: "Vdeo-Aulas",
              icon: mdiTelevision,
              route: "PacienteVideoAulas",
            },
            {
              title: "Eventos",
              icon: mdiCalendarStar,
              route: "PacienteEventos",
            },
          ],
        },
        {
          title: "Compras",
          icon: mdiCartVariant,
          subitems: [
            {
              title: "Produtos",
              icon: mdiGift,
              route: "PacienteProdutos",
            },
            {
              title: "Minhas compras",
              icon: mdiCartVariant,
              route: "PacienteCompras",
            },
          ],
        },
      ],
      icons: { mdiAccountDetailsOutline, mdiClose, mdiCamera, mdiFoodApple },
      color: "primary",
      right: false,
      permanent: true,
      miniVariant: false,
      expandOnHover: false,
      background: false,
    };
  },
  firestore () {
    return {
      userInfos: firebase
        .firestore()
        .collection("users")
        .doc(sessionStorage.getItem('current-user')),
      // .doc(firebase.auth().currentUser.email),
    };
  },
  // mounted () {
  //   if (this.$store.state.firestore.alimentos.length === 0) {
  //     this.$store.dispatch('firestore/getAlimentos')
  //   }
  // }
};
</script>