export async function getPreAtendimentoToFill({ commit }) {
  firebase
    .firestore()
    .collection("users")
    .doc(sessionStorage.getItem("current-user"))
    .get()
    .then(async (snapshot) => {
      commit("setPreAtendimentoToFill", await snapshot.data().preAtendimentoToFill);
    });
}

export function userLogin({ commit }, { email, password }) {
  firebase
    .auth()
    .signInWithEmailAndPassword(email, password)
    .then(async (user) => {
      commit("setUser", user.user.email);
      commit("setIsAuthenticated", true);
      commit("setAlertsLoginOk", false);

      async function getJoinDate() {
        return await firebase
          .firestore()
          .collection("users")
          .doc(user.user.email)
          .get()
          .then((snapshot) => {
            return snapshot.data().joinedAt.toMillis();
          });
      }

      let joinedAt = await getJoinDate();
      let daysIn = (new Date().getTime() - joinedAt) / 86400000;
      let daysInFull = Math.floor(daysIn);
      console.log(daysInFull);
      commit("setDaysIn", daysInFull);

      firebase
        .firestore()
        .collection("users")
        .doc(user.user.email)
        .get()
        .then(async (snapshot) => {
          commit("setUserInfo", await snapshot.data());
        });
      sessionStorage.setItem("isAuthenticated", true);
      sessionStorage.setItem("current-user", user.user.email);
      router.replace("/home");
    })
    .catch((error) => {
      console.log(error.message);
      sessionStorage.removeItem("isAuthenticated");
      commit("setUser", null);
      commit("setIsAuthenticated", false);
      commit("setAlertsLoginOk", true);
    });
}

