export function setUserPersonalData(
  { commit },
  {
    userInfos,
    userEmail,
    userPictureUrl,
    userSignatureUrl,
    userLogoUrl,
    useLogo,
  }
) {
  firebase
    .firestore()
    .collection("users")
    .doc(userEmail)
    .get()
    .then((response) => {
      firebase
        .firestore()
        .collection("users")
        .doc(userEmail)
        .update({
          name: userInfos.name,
          cpf: userInfos.cpf,
          crn: userInfos.crn,
          phone: userInfos.phone,
          phoneClients: userInfos.phoneClients,
          addressRoad: userInfos.addressRoad,
          addressComplement: userInfos.addressComplement,
          addressCep: userInfos.addressCep,
          addressCity: userInfos.addressCity,
          addressCountry: userInfos.addressCountry,
          addressBairro: userInfos.addressBairro,
          profilePicture:
            userPictureUrl != ""
              ? userPictureUrl
              : response.data().profilePicture,
          signature:
            userSignatureUrl != ""
              ? userSignatureUrl
              : response.data().signature,
          logo: userLogoUrl != "" ? userLogoUrl : response.data().logo,
          useLogo: useLogo,
        });

      var userInfo = {
        infos: userInfos,
        mail: userEmail,
        picture: userPictureUrl,
        sign: userSignatureUrl,
        logo: userLogoUrl,
      };

      commit("setUserInfo", userInfo);
      console.log(userInfo);
      console.log(commit);
    });
}

