export function pacientePreAtendimentoSubmit({ commit }, { nutri, pacienteInfos, pacienteEmail }) {
  firebase
    .firestore()
    .collection("users")
    .doc(nutri)
    .collection("pacientes")
    .doc(pacienteEmail)
    .update({
      pacienteInfos: pacienteInfos
    });

  firebase
    .firestore()
    .collection("users")
    .doc(pacienteEmail)
    .update({
      preAtendimentoToFill: false,
      pacienteInfos: pacienteInfos
    });

  commit("setPreAtendimentoToFill", false);

}

export function nutriPreAtendimentoSubmit({ commit }, { pacienteInfos, pacienteEmail }) {
  firebase
    .firestore()
    .collection("users")
    .doc(sessionStorage.getItem("current-user"))
    .collection("pacientes")
    .doc(pacienteEmail)
    .update({
      pacienteInfos: pacienteInfos
    });

  firebase
    .firestore()
    .collection("users")
    .doc(pacienteEmail)
    .update({
      preAtendimentoToFill: false
    });

  commit("setPreAtendimentoToFill", false);

  router.replace("/NovoAtendimento");
}

