export function salvarAtendimento({ commit }, { paciente, compos, medidas,
  fotoFrenteUrl, fotoAtrasUrl, fotoEsquerdaUrl, fotoDireitaUrl, bioimp, exams, sangue, anotacoes }) {
  var date = new Date();

  var day; var month;
  if (date.getDate() < 10) {
    day = "0" + date.getDate().toString();
  }
  if (date.getMonth() < 10) {
    day = "0" + date.getMonth().toString();
  }
  // var data = `${day}/${month}/${date.getFullYear().toString()}`
  var data = day + "/" + month + "/" + date.getFullYear().toString();

  var milliseconds = /*2524608000000 - */date.getTime();

  console.log(milliseconds);

  firebase
    .firestore()
    .collection("users")
    // .doc(sessionStorage.getItem("current-user"))
    // .collection("pacientes")
    .doc(paciente)
    .collection("atendimentos")
    .doc(milliseconds.toString())
    .set({
      data: milliseconds,
      compos: compos,
      medidas: medidas,
      fotoFrenteUrl: fotoFrenteUrl,
      fotoAtrasUrl: fotoAtrasUrl,
      fotoEsquerdaUrl: fotoEsquerdaUrl,
      fotoDireitaUrl: fotoDireitaUrl,
      bioimp: bioimp,
      exams: exams,
      sangue: sangue,
      anotacoes: anotacoes
    });

  firebase
    .firestore()
    .collection("users")
    // .doc(sessionStorage.getItem("current-user"))
    // .collection("pacientes")
    .doc(paciente)
    .collection("timeline")
    .doc(milliseconds.toString())
    .set({
      docId: milliseconds.toString(),
      colId: "atendimentos",
      nome: "avaliao",
      type: "atendimento",
      icon: "mdi-finance",
      date: data,
      to: "/PacienteEvolucao",
      text: "Uma nova avaliao foi feita!",
      toText: "Todas as avaliaes",
      color: "#614021",
      download: false
    });

  commit("dummySetter", "dummySet");

}

