export function salvarDietaPaciente({ commit }, { titulo, pacienteEmail, status, dietaRefeicoes }) {

  var date = new Date();

  var day; var month;
  if (date.getDate() < 10) {
    day = "0" + date.getDate().toString();
  }
  if (date.getMonth() < 10) {
    day = "0" + date.getMonth().toString();
  }
  // var data = `${day}/${month}/${date.getFullYear().toString()}`
  var data = day + "/" + month + "/" + date.getFullYear().toString();

  var millis = date.getTime();
  var milliseconds = 2524608000000 - date.getTime();

  firebase
    .firestore()
    .collection("users")
    .doc(pacienteEmail)
    .collection("dietasRefs")
    .doc(milliseconds.toString())
    .set({
      // .add({
      dietaDocId: milliseconds,
      titulo: titulo,
      kcal: status.KCAL,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      // refeicoes: dietaRefeicoes
    });

  firebase
    .firestore()
    .collection("users")
    .doc(pacienteEmail)
    .collection("dietas")
    .doc(milliseconds.toString())
    .set({
      // .add({
      isNew: true,
      dietaDocId: milliseconds,
      titulo: titulo,
      status: status,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      refeicoes: dietaRefeicoes
    });

  firebase
    .firestore()
    .collection("users")
    .doc(pacienteEmail)
    .collection("timeline")
    .doc(millis.toString())
    .set({
      docId: milliseconds.toString(),
      colId: "dietas",
      nome: titulo,
      type: "dieta",
      icon: "mdi-apple",
      date: data,
      to: "/PacienteDietas",
      text: "Uma nova dieta foi prescrita!",
      toText: "Todas as dietas",
      color: "#c5d13f",
      download: true
    });


  commit("dummySetter", "dummySet");
}

