<template>
  <div class="d-flex flex-column">
    <v-dialog v-model="salvoDialog" max-width="600">
      <v-card>
        <v-card-title class="d-flex justify-center">
          Movimento salvo no Livro Caixa
        </v-card-title>
        <v-card-text class="d-flex justify-center">
          A transao est transcrito no livro caixa. No pode mais ser
          alterado. Observaes podem ser feitas no fechamento da caixa diria.
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogDefinirPacotes" max-width="60%">
      <v-card>
        <v-card-title class="d-flex justify-center">
          Define os Pacotes Estticos
        </v-card-title>
        <v-form ref="formPacotesDialog" v-model="validPacotesDialog">
          <v-row
            v-for="pac in mov.pacotes"
            :key="pac.index"
            class="d-flex align-center"
          >
            <v-col cols="1" class="d-flex justify-end"> </v-col>
            <v-col cols="7">
              <v-select
                v-model="pac.nome"
                :items="pacotesEsteticos"
                label="Pacote"
                :rules="mandatory"
              >
              </v-select> </v-col
            ><v-col cols="3">
              <v-select
                v-model="pac.dataInicio"
                :items="meses"
                label="iniciando no..."
                :rules="mandatory"
              >
              </v-select> </v-col
            ><v-col cols="1"> </v-col>
          </v-row>
        </v-form>
        <v-card-actions class="d-flex justify-center mt-2">
          <v-btn color="#c5d13f" @click="checkFormPacotesDialog">
            Salvar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-card
      class="pa-5 my-1"
      tile
      style="background-color: rgba(197, 209, 63, 0.6)"
    >
      <v-form ref="formGeneral" v-model="validGeneral">
        <v-card
          :color="colorDirection(mov.direction)"
          class="d-flex justify-center white--text text-h6"
        >
          <v-row class="d-flex pa-2 white--text" style="background-color: #00">
            <v-col cols="0" md="3">
              <v-btn text class="white--text mt-4" @click="setNull(true)">
                Cancelar
              </v-btn>
            </v-col>
            <v-col cols="3" md="1">
              <v-switch
                inset
                v-model="mov.direction"
                color="#614021"
                @change="setNull(mov.direction)"
              >
              </v-switch>
            </v-col>
            <v-col cols="9" md="5">
              <v-row class="d-flex justify-start mt-4">{{
                labelDirection(mov.direction)
              }}</v-row>
            </v-col>
            <v-col cols="6" md="2">
              <v-row class="d-flex mt-4">{{ niceString(mov.valor) }}</v-row>
            </v-col>
            <v-col cols="6" md="1">
              <v-icon large color="white" class="mt-4">{{ formaIcon }}</v-icon>
            </v-col>
          </v-row>
        </v-card>

        <v-row>
          <v-col cols="0" md="3"></v-col>
          <v-col cols="12" md="6">
            <v-autocomplete
              :label="labelTipo(mov.direction)"
              :items="getItems(mov.direction)"
              v-model="mov.tipo"
              class="mt-5"
              single-line
              solo
              style="width: 100%"
              @change="setValor(mov.tipo)"
              :rules="mandatory"
              no-data-text="..."
            ></v-autocomplete> </v-col
          ><v-col cols="0" md="3"></v-col>
        </v-row>

        <div v-if="isPacCons">
          <v-row>
            <v-col cols="0" md="3"></v-col>
            <v-col cols="12" md="6">
              <v-row class="d-flex justify-center">
                <v-autocomplete
                  label="Nmero de Pacotes"
                  :items="pacCons"
                  v-model="mov.descr"
                  single-line
                  class="mt-2"
                  solo
                  style="width: 100%"
                  @change="setValorPacCons(mov.descr)"
                  :rules="mandatory"
                  no-data-text="..."
                ></v-autocomplete> </v-row
              ><v-row class="d-flex justify-center" v-if="mov.descr !== null">
                <v-btn
                  small
                  class="white--text"
                  color="#614021"
                  @click="dialogDefinirPacotes = true"
                  :rules="mandatory"
                >
                  definir Pacotes
                </v-btn>
              </v-row></v-col
            ><v-col cols="0" md="3"></v-col>
          </v-row>
        </div>

        <div v-if="isPacote">
          <v-row>
            <v-col cols="0" md="3"></v-col>
            <v-col cols="12" md="6">
              <v-select
                :value="mov.descr"
                v-model="mov.descr"
                :items="pacotesEsteticos"
                solo
                label="Pacote"
                style="width: 100%"
                @input="setValorPacote(mov.descr)"
                :rules="mandatory"
              >
              </v-select> </v-col
            ><v-col cols="0" md="3"></v-col>
          </v-row>
        </div>

        <div v-if="isSessao">
          <v-row>
            <v-col cols="0" md="3"></v-col>
            <v-col cols="12" md="6">
              <v-select
                :value="mov.descr"
                v-model="mov.descr"
                :items="sessoesEsteticos"
                solo
                label="Sesso"
                style="width: 100%"
