                :rules="mandatory"
              >
              </v-select> </v-col
            ><v-col cols="0" md="3"></v-col>
          </v-row>
        </div>

        <v-divider></v-divider>
        <div v-if="descrNesc">
          <v-row>
            <v-col cols="3" md="2" class="d-flex align-center justify-end">
              Descrio:
            </v-col>
            <v-col cols="9" md="4">
              <v-text-field
                label='ex.: "Exame"'
                single-line
                solo
                class="mt-3 mx-3"
                style="width: 100%"
                v-model="mov.descr"
                :rules="mandatory"
              ></v-text-field>
            </v-col>
            <v-col cols="3" md="2" class="d-flex align-center justify-end">
              Valor:
            </v-col>
            <v-col cols="9" md="4">
              <v-text-field
                label='ex.: "150"'
                single-line
                solo
                class="mt-3 mx-3"
                style="width: 100%"
                v-model="mov.valor"
                :rules="mandatory"
              ></v-text-field>
            </v-col>
          </v-row>
        </div>
        <div v-else>
          <div v-if="mov.direction">
            <v-row>
              <v-col cols="12" md="3" class="d-flex align-center justify-end">
                <v-checkbox
                  v-model="descontoAct"
                  label="Desconto"
                  color="#614021"
                  @change="descontoImpact"
                ></v-checkbox>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  label='desconto em R$, ex.: "100"'
                  single-line
                  :solo="descontoAct"
                  class="mt-3 mx-3"
                  style="width: 100%"
                  v-model="mov.desconto"
                  @change="adjustValor(mov.desconto)"
                  :disabled="!descontoAct"
                ></v-text-field> </v-col
              ><v-col cols="12" md="3"></v-col>
            </v-row>
          </div>
        </div>
        <v-divider></v-divider>

        <v-row>
          <v-col cols="12" md="3" class="d-flex align-center justify-end">
            Forma de Pagamento:
          </v-col>
          <v-col cols="12" md="6">
