              :items="pagamentoFormas"
              v-model="mov.forma"
              class="mt-5"
              single-line
              solo
              style="width: 100%"
              :rules="mandatory"
            ></v-select> </v-col
          ><v-col cols="0" md="3"></v-col>
        </v-row>
