  computed: {
    pacientes() {
      return this.$store.state.firestore.pacientes;
    },
    formaIcon() {
      if (this.mov.forma === 1) {
        return this.icons.mdiCash100;
      } else if (this.mov.forma === 2) {
        return this.icons.mdiCreditCardCheckOutline;
      } else if (this.mov.forma === 3) {
        return this.icons.mdiCreditCardClockOutline;
      } else if (this.mov.forma === 4) {
        return this.icons.mdiBankTransfer;
      } else {
        return this.icons.mdiHelpRhombusOutline;
      }
    },
    descrNesc() {
      if (this.mov.tipo === "Outro") {
        return true;
      } else {
        return false;
      }
    },
    isPacote() {
      if (this.mov.direction && this.mov.tipo === "Pacote Esttico") {
        return true;
      } else {
        return false;
      }
    },
    isSessao() {
      if (this.mov.direction && this.mov.tipo === "Sesso Esttica") {
        return true;
      } else {
        return false;
      }
    },
    isPacCons() {
      if (
        this.mov.direction &&
        this.mov.tipo === "3 Consultas + Pacote Esttico"
      ) {
        return true;
      } else {
        return false;
      }
    },
    is3Cons() {
      if (this.mov.direction && this.mov.tipo === "3 Consultas") {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    labelDirection(dirBool) {
      if (dirBool) {
        return "Venda Nova";
      } else {
        return "Gasto Novo";
      }
    },
    colorDirection(dirBool) {
      if (dirBool) {
        return "green";
      } else {
        return "red";
      }
    },
    labelTipo(dirBool) {
      if (dirBool) {
        return "Tipo de Venda";
      } else {
        return "Tipo de Gasto";
      }
    },
    getItems(dirBool) {
      if (dirBool) {
        return this.vendaItems;
      } else {
        return this.gastoItems;
      }
    },
    // pacienteSelected(pacienteId) {
    //   if (pacienteId != undefined) {
    //     var paciente = this.pacientes.find((item) => item.value == pacienteId);
    //     this.mov.paciente = paciente;
    //     // this.salvarDietaEmPaciente = true;
    //     // this.salvarDietaEmSalvas = false;
    //   } else {
    //     this.mov.paciente = null;
    //     // this.pacienteChosen = { data: { nome: "" } };
    //     // this.salvarDietaEmPaciente = false;
    //     // this.salvarDietaEmSalvas = true;
    //   }
    // },
    niceString(valor) {
      var valorKomma;
      if (valor > 1000) {
        var tusiger = Math.floor(valor / 1000);
        var rest = valor % 1000;
        valorKomma = tusiger.toString() + "." + rest.toString();
      } else {
        valorKomma = valor.toString();
      }

      return "R$ " + valorKomma + ",00";
    },
    adjustValor(desconto) {
      if (!isNaN(desconto) && this.descontoAct) {
        this.mov.valor -= desconto;
      }
    },
    descontoImpact() {
      if (!this.descontoAct) {
        this.setValor(this.mov.tipo);
        if (this.mov.tipo === "Pacote Esttico") {
          this.setValorPacote(this.mov.descr);
        }
        if (this.mov.tipo === "Sesso Esttica") {
          this.setValorSessao(this.mov.descr);
        }
        if (this.mov.tipo === "3 Consultas + Pacote Esttico") {
          this.setValorPacCons(this.mov.descr);
        }
      } else {
        this.adjustValor(this.mov.desconto);
      }
    },
    setValor(tipo) {
      if (this.mov.direction) {
        var index = this.vendaItems.indexOf(tipo);
        this.mov.valor = this.vendaValores[index];
      } else {
        var index2 = this.gastoItems.indexOf(tipo);
        this.mov.valor = this.gastoValores[index2];
      }
    },
    setValorPacote(descr) {
      this.mov.descr = descr;
      var index = this.pacotesEsteticos.indexOf(descr);
      this.mov.valor = this.pacotesEsteticosValores[index];
    },
    setValorSessao(descr) {
      this.mov.descr = descr;
      var index = this.sessoesEsteticos.indexOf(descr);
      this.mov.valor = this.sessoesEsteticosValores[index];
    },
    setValorPacCons(descr) {
      var index = this.pacCons.indexOf(descr);
      this.mov.valor = this.pacConsValores[index];

      this.mov.pacotes = [];
      for (var j = 0; j <= index; j++) {
        this.mov.pacotes.push({
          nome: null,
          dataInicio: null,
          index: j,
        });
      }
    },
    setNull(dir) {
      this.mov = {
        direction: dir,
        tipo: null,
        descr: null,
        paciente: null,
        valor: 0,
        pacotes: [],
        forma: null,
      };
      this.temasToLiberate = [];
    },
    checkFormPacotesDialog() {
      if (this.$refs.formPacotesDialog.validate()) {
        this.dialogDefinirPacotes = false;
      }
    },
    salvarMov() {
      var d = new Date();
      var milliseconds = d.getTime();
      this.mov.millis = milliseconds;
      this.mov.y = d.getFullYear();
      this.mov.m = d.getMonth() + 1;
      this.mov.d = d.getDate();
      if (this.mov.paciente !== null) {
        var getName = function (id) {
          var el = this.pacientes.find((item) => item.value == id);
          return el.text;
        };
        this.mov.pacienteName = getName(this.mov.paciente);
      } else {
        this.mov.pacienteName = "";
      }
      this.mov.descontoAct = this.descontoAct;

      if (this.mov.tipo === "Pacote Esttico") {
        this.mov.pacotes = [{
          nome: this.mov.descr,
          dataInicio: 1,
          index: 1
        }]
      }

      if (this.$refs.formGeneral.validate()) {
        console.log(this.mov);
        if (this.mov.direction) {
          firebase
            .firestore()
            .collection("users")
            .doc(sessionStorage.getItem("current-user"))
            .collection("caixa")
            .doc(milliseconds.toString())
            .set(this.mov);

          if (this.mov.paciente !== null) {
            firebase
              .firestore()
              .collection("users")
              .doc(this.mov.paciente)
              .collection("compras")
              .doc(milliseconds.toString())
              .set(this.mov);

            var objs = this.objs;
            var pacienteDocID = this.mov.paciente;

            this.temasToLiberate.forEach(function (tema) {
              var obj = objs[tema];
              firebase
                .firestore()
                .collection("users")
                .doc(pacienteDocID)
                .collection("videoAulas")
                .doc(tema)
                .set(obj);
            });
          }
          this.salvoDialog = true;
          this.setNull();
        } else {
          this.mov.valor = -1 * this.mov.valor;
          firebase
            .firestore()
            .collection("users")
            .doc(sessionStorage.getItem("current-user"))
            .collection("caixa")
            .doc(milliseconds.toString())
            .set(this.mov);

          this.salvoDialog = true;
          this.precisaAgendamento();
          this.setNull();
        }
      }
    },
    precisaAgendamento() {
      var precisam = [
        "1 Consulta",
        "3 Consultas",
        "3 Consultas + Pacote Esttico",
        "Pacote Esttico",
        "Sesso Esttica",
      ];
      if (precisam.includes(this.mov.tipo)) {
        this.$store.dispatch("firestore/agendaMov", { mov: this.mov });
      }
    },
  },
  mounted() {
    if (this.$store.state.firestore.pacientes.length === 0) {
      this.$store.dispatch("firestore/getPacientes");
    }
  },
};
</script>