<template>
  <div>
    <v-dialog v-model="visualizarDialog" width="600">
      <v-card>
        <v-card-title class="d-flex justify-center text-h6">
          {{ nomeFormulaPaciente }}, {{ pacienteChosen.data.nome }}
        </v-card-title>
        <v-card-text>
          <div
            v-for="formula in prescricaoC.formulasList"
            :key="formula.titulo"
          >
            <v-row class="mx-4"> {{ formula.titulo }} </v-row>
            <v-row class="mx-4">
              <v-textarea
                v-model="formula.prescricoes"
                solo
                height="200"
              ></v-textarea>
            </v-row>
          </div>
        </v-card-text>
        <v-card-actions class="d-flex justify-center">
          <v-btn text @click="visualizarDialog = false" color="#614021">
            Fechar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <div class="d-flex flex-column justify-center">
      <v-card
        class="d-inline-block mr-3"
        width="100%"
        elevation="3"
        color="grey lighten-2"
      >
        <v-container>
          <v-row class="mx-4 mb-2">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  v-on="on"
                  class="mx-2"
                  @click="visualizarDialog = true"
                >
                  <v-icon color="#614021">{{ icons.mdiEye }}</v-icon>
                </v-btn>
              </template>
              <span>Visualizar Prescrio</span>
            </v-tooltip>

            <v-spacer></v-spacer>
            <h3>Prescrever Frmulas</h3>
            <v-spacer></v-spacer>

            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="#c5d13f"
                  v-bind="attrs"
                  v-on="on"
                  @click="createDocWithJSPDF(prescricaoC)"
                >
                  <v-icon color="#614021">{{ icons.mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Baixar Prescrio</span>
            </v-tooltip>

            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="#c5d13f"
                  v-bind="attrs"
                  v-on="on"
                  class="mx-4"
                  @click="salvarPrescricao"
                >
                  <v-icon color="#614021">{{ icons.mdiContentSave }}</v-icon>
                </v-btn>
              </template>
              <span>Salvar Prescrio</span>
            </v-tooltip>
          </v-row>
          <v-divider></v-divider>
          <v-row class="d-flex justify-center mx-4 mt-2">
            <v-text-field
              dense
              solo
              v-model="nomeFormulaPaciente"
              label="Titulo da Prescrio (visvel pelo paciente)"
              class="align-self-center mx-2"
              required
            ></v-text-field>
            <v-autocomplete
              label="Nome do Paciente"
              single-line
              solo
              dense
              clearable
              v-model="pacienteDocID"
              :items="pacientes"
              class="align-self-center mx-2"
              @change="pacienteSelected(pacienteDocID)"
              no-data-text="..."
            ></v-autocomplete>
          </v-row>
        </v-container>
      </v-card>

      <PrescreverForm></PrescreverForm>
    </div>
  </div>
</template>

<script>
// import firebase from "firebase";
import {
  mdiCalendar,
  mdiContentSave,
  mdiPrinter,
  mdiEye,
  mdiEmailSendOutline,
  mdiDownload,
  mdiMagnify,
} from "@mdi/js";

import jspdf from "jspdf";
import "jspdf-autotable";

// components imports
import PrescreverForm from "../components/home/formulas/PrescreverForm";
import firebase from "firebase";
// import router from "../router";

export default {
  components: { PrescreverForm },
  data: function () {
    return {
      dialogSalvar: false,
      pacienteDocID: "",
      pacienteChosen: { data: { nome: "" } },
      visualizarDialog: false,
      icons: {
        mdiCalendar,
        mdiContentSave,
        mdiPrinter,
        mdiEye,
        mdiEmailSendOutline,
        mdiDownload,
        mdiMagnify,
      },
      nomeFormulaPaciente: "",
      preloadLogo: new Image(),
      preloadSignature: new Image(),
    };
  },
  computed: {
    pacientes() {
      return this.$store.state.firestore.pacientes;
    },
    formulas() {
      return this.$store.state.firestore.formulas;
    },
    prescricaoC() {
      return this.$store.state.firestore.prescricaoC;
    },
  },
  methods: {
    // async preloadImages() {
    //   this.preloadLogo.crossOrigin = "anonymous";
    //   this.preloadSignature.crossOrigin = "anonymous";
    //   this.preloadLogo.src = `${this.$store.state.firestore.userInfo.logo}`;
    //   this.preloadSignature.src = `${this.$store.state.firestore.userInfo.signature}`;
    //   this.preloadLogo.onload = function () {
    //     console.log("Logo carregada");
    //   };
    //   this.preloadSignature.onload = function () {
    //     console.log("Signature carregada");
    //   };

    //   console.log(
    //     "natural:",
    //     this.preloadLogo.naturalWidth,
    //     this.preloadLogo.naturalHeight
    //   );
    //   console.log(
    //     "natural:",
    //     this.preloadSignature.naturalWidth,
    //     this.preloadSignature.naturalHeight
    //   );
    // },
    async createDocWithJSPDF(prescricaoC) {
      if (this.prescricaoC.formulasList.length !== 0) {
        alert("Gerando PDF");
        // links
        // https://mrrio.github.io/jsPDF/examples/basic.html
        // http://www.rotisedapsales.com/snr/cloud_staging/website/jsPDF-master/docs/global.html#text
        // https://simonbengtsson.github.io/jsPDF-AutoTable/
        // https://www.npmjs.com/package/jspdf-autotable

        var doc = new jspdf("p");
        var date = new Date();
        var day = date.getDate();
        var month = date.getMonth() + 1;
        var year = date.getFullYear();

        // Doc settings
        var fontSize = 12;
        doc.setFontSize(fontSize);
        doc.setFont("Times");

        // The (900 / 262) division is used to keep the proportion of the original image
        // New Width * (900 / 262) will keep the height proportion

        var imgWidth = 40;
        var imgHeight = imgWidth / (900 / 262);
        var imgPos = (210 - imgWidth) / 2;
        var startXPos = 15;
        var textStartYPos = imgHeight * 4;

        var centeredText = function (text, y) {
          var textWidth =
            (doc.getStringUnitWidth(text) * doc.internal.getFontSize()) /
            doc.internal.scaleFactor;
          var textOffset = (doc.internal.pageSize.width - textWidth) / 2;
          doc.text(textOffset, y, text);
        };

        var createTexts = function (
          textList,
          centered = false,
          textStart = textStartYPos
        ) {
          for (var i = 0; i < textList.length; i++) {
            if (centered) {
              centeredText(textList[i], textStart + (fontSize / 2) * (1 + i));
            } else {
              var lines = doc
                .setFont("Roboto")
                .splitTextToSize(textList[i], 180);
              // console.log(lines)
              for (var j = 0; j < lines.length; j++) {
                doc.text(
                  startXPos,
                  textStart + (fontSize / 2) * (1 + j),
                  lines[j]
                );
              }
              textStart += (fontSize / 2) * /*1 + */ lines.length;
            }
          }
          return (textStart += fontSize / 2);
        };

        // console.log(this.$store.state.firestore.userInfo);
        // // Logo Clubedonutricionista
        // if (this.$store.state.firestore.userInfo.useLogo === false) {
        console.log("op 1");
        doc.addImage({
          imageData: require("../assets/logoCompleto.png"),
          y: startXPos,
          x: imgPos,
          w: imgWidth,
          h: imgHeight,
        });
        // } else {
        //   // LOGO CLUBE
        //   imgWidth = 30;
        //   imgHeight = imgWidth / (900 / 262);
        //   startXPos = 15;

        //   doc.addImage({
        //     imageData: require("../assets/logoCompleto.png"),
        //     y: startXPos,
        //     x: 15,
        //     w: imgWidth,
        //     h: imgHeight,
        //   });

        //   imgWidth = 50;
        //   var startYPos = 15 + imgHeight * 2;
        //   imgHeight =
        //     imgWidth /
        //     (this.preloadSignature.naturalWidth /
        //       this.preloadSignature.naturalHeight);
        //   imgPos = (210 - imgWidth) / 2;
        //   textStartYPos = imgHeight + 4 * (imgHeight / fontSize) + startYPos;

        //   doc.addImage({
        //     imageData: this.preloadSignature,
        //     y: startYPos,
        //     x: imgPos,
        //     w: imgWidth,
        //     h: imgHeight,
        //   });
        // }

        var texts = [
          `Prescrio de ${this.nomeFormulaPaciente} (${day}/${month}/${year})`,
          `Por: ${this.pacienteChosen.data.nome}`,
        ];

        var endTablePosY = createTexts(texts);

        // createTexts(
        //   [
        //     "_______________________________________________________",
        //     "Possveis Substitues de Alimentos",
        //   ],
        //   true,
        //   endTablePosY
        // );

        for (var i = 0; i < prescricaoC.formulasList.length; i++) {
          createTexts(
            [
              prescricaoC.formulasList[i].titulo,
              prescricaoC.formulasList[i].prescricoes,
            ],
            false,
            endTablePosY + i * 48
          );
        }

        // USE SIGNATURE
        console.log(
          "signature",
          this.$store.state.firestore.userInfo.signature
        );
        // if (this.$store.state.firestore.userInfo.signature != undefined) {
        //   imgWidth = 30;
        //   startYPos =
        //     fontSize +
        //     (endTablePosY !== undefined
        //       ? endTablePosY + 16
        //       : textStartYPos + texts.length * (fontSize * (3 / 4)));
        //   imgHeight =
        //     imgWidth /
        //     (this.preloadLogo.naturalWidth / this.preloadLogo.naturalHeight);
        //   imgPos = (210 - imgWidth) / 2;
        //   textStartYPos = imgHeight + 4 * (imgHeight / fontSize) + startYPos;

        //   doc.addImage({
        //     imageData: this.preloadSignature,
        //     y: startYPos,
        //     x: imgPos,
        //     w: imgWidth,
        //     h: imgHeight,
        //   });
        // } else {
        imgHeight = 0;
        // }

        createTexts(
          [
            "____________________________",
            `Nutricionista ${this.$store.state.firestore.userInfo.name}`,
            `CRN: ${this.$store.state.firestore.userInfo.crn}`,
            `Data: ${day}/${month}/${year}`,
          ],
          true,
          endTablePosY !== undefined
            ? endTablePosY +
                prescricaoC.formulasList.length * 48 +
                16 +
                imgHeight * 1.4
            : textStartYPos + texts.length * (fontSize * (3 / 4))
        );

        doc.save("prescricao.pdf");
      } else {
        alert("Preencha todos os campos da dieta antes de continuar");
      }
    },
    pacienteSelected(pacienteId) {
      if (pacienteId != undefined) {
        var paciente = this.pacientes.find((item) => item.value == pacienteId);
        this.pacienteChosen = paciente;
      } else {
        this.pacienteChosen = { data: { nome: "" } };
      }
    },
    salvarPrescricao() {
      if (this.pacienteDocID !== "") {
        var id = firebase
          .firestore()
          .collection("users")
          .doc(this.pacienteDocID)
          .collection("formulas")
          .doc();

        firebase
          .firestore()
          .collection("users")
          .doc(this.pacienteDocID)
          .collection("formulas")
          .doc(id)
          .set({
            id: id,
            nome: this.nomeFormulaPaciente,
            prescricaoC: this.prescricaoC,
          });
      } else {
          alert("Escolhe um paciente antes de salvar!")
      }
    },
  },
  mounted() {
    if (this.$store.state.firestore.pacientes.length === 0) {
      this.$store.dispatch("firestore/getPacientes");
    }
    // this.preloadImages();
  },
};
</script>

