<template>
  <div class="d-flex flex-column justify-center">
    <v-expansion-panels class="mb-4" dark>
      <v-expansion-panel v-model="panel0">
        <v-expansion-panel-header class="text-h6">
          Dietas salvas
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-card v-for="dieta in dietasSalvas" :key="dieta.titulo" outlined>
            <v-row class="d-flex justify-center">
              <v-col cols="12" class="d-flex justify-space-between">
                <v-row class="text-subtitle-1 ml-2 d-flex align-center">
                  {{ dieta.titulo }}, {{ dieta.kcal }} kcal (
                  {{ dieta.createdAt.toDate().getDate() }}/{{
                    dieta.createdAt.toDate().getMonth() + 1
                  }}/{{ dieta.createdAt.toDate().getYear() + 1900 }}
                  {{ dieta.createdAt.toDate().getHours() }}:{{
                    stringify(dieta.createdAt.toDate().getMinutes())
                  }}
                  )
                </v-row>
                <v-btn
                  color="#c5d13f"
                  @click="setDietaEditar(dieta.dietaDocId)"
                  class="mr-2 black--text"
                >
                  Editar
                </v-btn>
              </v-col>
            </v-row>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-card
      class="d-inline-block mr-3"
      width="100%"
      elevation="3"
      color="grey lighten-2"
    >
      <v-container>
        <v-card-actions>
          <v-dialog
            v-model="dialogVisualizar"
            max-width="80%"
            height="80%"
            multiple
          >
            <v-card>
              <v-expansion-panels v-model="panel" multiple>
                <v-expansion-panel
                  v-for="refeicao in dietaRefeicoes"
                  :key="refeicao.nomeRefeicao"
                >
                  <v-expansion-panel-header>
                    <v-row class="text-h6">
                      {{ refeicao.nomeRefeicao }},
                      {{ refeicao.horarioRefeicao }}
                    </v-row>
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-expansion-panels
                      v-model="panelSubs"
                      multiple
                      v-show="refeicao.alimentosList[0].gramas > 0"
                    >
                      <v-expansion-panel
                        v-for="alimento in refeicao.alimentosList"
                        :key="alimento.nome"
                      >
                        <v-expansion-panel-header>
                          <p
                            v-if="alimento.quantidade < 2"
                            class="text-p black--text"
                          >
                            {{ alimento.quantidade }} {{ alimento.medida }} de
                            {{ alimento.nome }} ({{ alimento.gramas }}g total)
                          </p>
                          <p
                            v-else-if="alimento.quantidade > 1"
                            class="text-p black--text"
                          >
                            {{ alimento.quantidade }} ({{ alimento.medida }}) de
                            {{ alimento.nome }} ({{ alimento.gramas }}g total)
                          </p>
                          <p v-else class="text-p black--text">
                            {{ alimento.gramas }}g de {{ alimento.nome }}
                          </p>
                          <template v-slot:actions>
                            <v-icon
                              color="black"
                              v-if="alimento.toSubstitute != undefined"
                              @click="log(alimento.toSubstitute)"
                            >
                              {{ icons.mdiAutorenew }}
                            </v-icon>
                            <v-icon v-else> </v-icon>
                          </template>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content class="ml-6">
                          <v-row
                            class="mt-1"
                            v-show="alimento.toSubstitute != undefined"
                          >
                            <strong> Possveis substituies </strong>
                          </v-row>
                          <v-simple-table
                            v-show="alimento.toSubstitute != undefined"
                          >
                            <template v-slot:default>
                              <thead>
                                <tr>
                                  <th class="text-left">Alimento</th>
                                  <th class="text-left">Quantidade</th>
                                  <th class="text-left">Medida caseira</th>
                                  <th class="text-left">Gramas totais</th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr
                                  v-for="sub in alimento.toSubstitute"
                                  :key="sub.qtdSub"
                                >
                                  <td>{{ sub.nome }}</td>
                                  <td>{{ sub.qtdSub }}</td>
                                  <td>{{ sub.medida_caseira_padrao }}</td>
                                  <td>{{ sub.gramas }}g</td>
                                </tr>
                              </tbody>
                            </template>
                          </v-simple-table>
                        </v-expansion-panel-content>
                      </v-expansion-panel>
                    </v-expansion-panels>

                    <v-expansion-panels
                      v-model="panelReceitas"
                      multiple
                      v-show="refeicao.receitasList[0].porcoes > 0"
                    >
                      <v-expansion-panel
                        v-for="receita in refeicao.receitasList"
                        :key="receita.titulo"
                      >
                        <v-expansion-panel-header class="text-p black--text">
                          {{ receita.porcoes }} {{ porcao(receita.porcoes) }} de
                          "{{ receita.titulo }}"
                        </v-expansion-panel-header>
                        <v-expansion-panel-content class="ml-6">
                          <v-row> <strong> Modo de Preparo: </strong> </v-row>
                          <v-row> {{ receita.modoPreparo }} </v-row>
                          <v-row class="mt-1">
                            <strong> Ingredientes: </strong>
                          </v-row>
                          <v-simple-table>
                            <template v-slot:default>
                              <thead>
                                <tr>
                                  <th class="text-left">Alimento</th>
                                  <th class="text-left">Quantidade</th>
                                  <th class="text-left">Medida caseira</th>
                                  <th class="text-left">Gramas totais</th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr
                                  v-for="alimentoR in receita.alimentosList"
                                  :key="alimentoR.nome"
                                >
                                  <td>{{ alimentoR.nome }}</td>
                                  <td>{{ alimentoR.quantidade }}</td>
                                  <td>{{ alimentoR.medida }}</td>
                                  <td>{{ alimentoR.gramas }}g</td>
                                </tr>
                              </tbody>
                            </template>
                          </v-simple-table>
                        </v-expansion-panel-content>
                      </v-expansion-panel>
                    </v-expansion-panels>
                    <v-card class="pr-2" min-height="50px" elevation="1">
                      <v-card-text
                        v-show="refeicao.observacao"
                        class="text-p black--text ml-2"
                      >
                        <strong>Observaes: </strong> {{ refeicao.observacao }}
                      </v-card-text>
                    </v-card>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogDeletar" max-width="640" multiple>
            <v-card>
              <v-card-title>
                Deseja deletar a dieta "{{ dietaTitulo }}" ?</v-card-title
              >
              <v-card-text>
                Ao concordar sair da lista das suas dietas salvas de forma
                irrevogvel
              </v-card-text>
              <v-card-actions>
                <v-btn @click="dialogDeletar = false" text color="#614021"
                  >Fechar</v-btn
                >
                <v-spacer></v-spacer>
                <v-btn @click="deletarDieta" color="red" class="white--text">
                  Deletar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                class="mx-2"
                @click="dialogVisualizar = true"
              >
                <v-icon color="#614021">{{ icons.mdiEye }}</v-icon>
              </v-btn>
            </template>
            <span>Visualizar Dieta</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" class="mx-2">
                <v-icon color="#614021">{{ icons.mdiPrinter }}</v-icon>
              </v-btn>
            </template>
            <span>Imprimir Dieta</span>
          </v-tooltip>

          <v-tooltip bottom color="red">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                class="mx-2"
                @click="dialogDeletar = true"
              >
                <v-icon color="red">{{ icons.mdiTrashCanOutline }}</v-icon>
              </v-btn>
            </template>
            <span>Deletar Dieta</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="#c5d13f"
                v-bind="attrs"
                v-on="on"
                @click="createDocWithJSPDF"
              >
                <v-icon color="#614021">{{ icons.mdiDownload }}</v-icon>
              </v-btn>
            </template>
            <span>Baixar Dieta</span>
          </v-tooltip>

          <v-dialog v-model="dialogSalvar" max-width="640" multiple>
            <v-card color="white">
              <v-card-title>Salvar Dieta</v-card-title>
              <v-card-text>
                <v-row>
                  <v-checkbox
                    label="No histrico do paciente:"
                    color="#614021"
                    v-model="salvarDietaEmPaciente"
                  ></v-checkbox>
                  <v-autocomplete
                    label="Nome do Paciente"
                    single-line
                    solo
                    dense
                    clearable
                    v-model="pacienteDocID"
                    :items="pacientes"
                    class="align-self-center mx-2"
                    @change="pacienteSelected(pacienteDocID)"
                    no-data-text="..."
                  ></v-autocomplete>
                </v-row>
                <v-row>
                  <v-checkbox
                    color="#614021"
                    v-model="salvarDietaEmSalvas"
                    label='Nas minhas "Dietas salvas"'
                  ></v-checkbox>
                  <v-text-field
                    dense
                    solo
                    v-model="nomeDieta"
                    label="Titulo da dieta"
                    class="align-self-center mx-2"
                    required
                    :rules="mandatory"
                  ></v-text-field>
                </v-row>
              </v-card-text>
              <v-divider></v-divider>
              <v-card-actions>
                <v-btn @click="salvarDieta" color="#c5d13f">
                  <v-icon color="#614021">{{ icons.mdiContentSave }}</v-icon>
                  Salvar</v-btn
                >
                <v-spacer></v-spacer>
                <v-btn @click="dialogSalvar = false" text color="grey"
                  >Fechar</v-btn
                >
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="#c5d13f"
                v-bind="attrs"
                v-on="on"
                class="mx-4"
                @click="dialogSalvar = true"
              >
                <v-icon color="#614021">{{ icons.mdiContentSave }}</v-icon>
              </v-btn>
            </template>
            <span>Salvar Dieta</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="#c5d13f" v-bind="attrs" v-on="on">
                <v-icon color="#614021">{{ icons.mdiEmailSendOutline }}</v-icon>
              </v-btn>
            </template>
            <span>Enviar Dieta ao Paciente</span>
          </v-tooltip>
        </v-card-actions>

        <v-divider></v-divider>

        <v-row class="d-flex justify-center">
          <v-col cols="12" md="8">
            <v-tooltip top>
              <template v-slot:activator="{ on, attrs }">
                <v-card
                  v-bind="attrs"
                  v-on="on"
                  elevation="0"
                  color="grey lighten-2"
                >
                  <v-autocomplete
                    label="Nome do Paciente"
                    single-line
                    solo
                    clearable
                    class="mt-2 mx-3"
                    v-model="pacienteDocID"
                    :items="pacientes"
                    @change="pacienteSelected(pacienteDocID)"
                    no-data-text="..."
                  ></v-autocomplete>
                </v-card>
              </template>
              <span
                >Selecione o nome de um paciente cadastrado para anexar a dieta
              </span>
            </v-tooltip>
          </v-col>
          <v-col cols="12" md="4">
            <v-btn class="mt-3">Ver histrico de atendimento</v-btn>
          </v-col>
        </v-row>
        <v-row class="d-flex justify-center black--text text-h6 mb-2">
          {{ dietaTitulo }}
        </v-row>
        <v-row class="d-flex justify-center">
          <v-chip color="#c5d13f" class="d-flex align-center">
            <v-row class="px-2 black--text">
              <p class="px-2 mt-4">
                KCAL: <strong>{{ dietaStatus.KCAL.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                CHO: <strong>{{ dietaStatus.CHO.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                PTN: <strong>{{ dietaStatus.PTN.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                LIP: <strong>{{ dietaStatus.LIP.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                COLEST: <strong>{{ dietaStatus.COL.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                Na: <strong>{{ dietaStatus.SOD.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                FIB: <strong>{{ dietaStatus.FIB.toFixed(2) }}</strong>
              </p>
            </v-row>
          </v-chip>
          <v-dialog v-model="dialog" width="90%">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                fab
                color="#c5d13f"
                class="mx-2"
                x-small
                v-bind="attrs"
                v-on="on"
              >
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }"
                    ><v-icon color="#614021" v-bind="attrs" v-on="on">{{
                      icons.mdiDotsHorizontal
                    }}</v-icon>
                  </template>
                  <span>Mais informaes nutricionais</span>
                </v-tooltip>
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="h6"
                  >Informaes nutricionais detalhadas da dieta</span
                >
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-simple-table>
                    <template v-slot:default>
                      <tbody>
                        <tr
                          v-for="nutrient in allNutrients"
                          :key="nutrient.codigo"
                        >
                          <td>{{ nutrient.nome }}:</td>
                          <td>
                            <strong>{{
                              dietaStatus[nutrient.codigo].toFixed(2)
                            }}</strong>
                          </td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-container>
              </v-card-text>
              <v-card-actions class="d-flex justify-center">
                <v-btn color="#c5d13f" text @click="dialog = false">
                  Fechar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-container>
    </v-card>

    <DietaFormEdit></DietaFormEdit>

    <template>
      <v-snackbar
        timeout="-1"
        elevation="0"
        bottom
        right
        multi-line
        v-model="snack"
        color="rgba(0, 0, 0, 0)"
      >
        <div>
          <v-row class="d-flex justify-center align-center">
            <v-card width="600px" color="rgba(0, 0, 0, 0)" elevation="0">
              <v-row class="d-flex justify-center">
                <v-chip color="#c5d13f" class="d-flex align-center">
                  <v-row class="d-flex justify-center px-2 black--text">
                    <p class="px-2 mt-4">
                      KCAL: <strong>{{ dietaStatus.KCAL.toFixed() }}</strong>
                    </p>
                    <p class="px-2 mt-4">
                      CHO: <strong>{{ dietaStatus.CHO.toFixed() }}</strong>
                    </p>
                    <p class="px-2 mt-4">
                      PTN: <strong>{{ dietaStatus.PTN.toFixed() }}</strong>
                    </p>
                    <p class="px-2 mt-4">
                      LIP: <strong>{{ dietaStatus.LIP.toFixed() }}</strong>
                    </p>
                    <p class="px-2 mt-4">
                      COLEST: <strong>{{ dietaStatus.COL.toFixed() }}</strong>
                    </p>
                    <p class="px-2 mt-4">
                      Na: <strong>{{ dietaStatus.SOD.toFixed() }}</strong>
                    </p>
                    <p class="px-2 mt-4">
                      FIB: <strong>{{ dietaStatus.FIB.toFixed() }}</strong>
                    </p>
                  </v-row>
                </v-chip>
              </v-row>
            </v-card>
            <v-btn
              fab
              color="#c5d13f"
              class="mx-2 my-1"
              x-small
              @click="dialog = true"
            >
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }"
                  ><v-icon color="#614021" v-bind="attrs" v-on="on">{{
                    icons.mdiDotsHorizontal
                  }}</v-icon>
                </template>
                <span>Mais informaes nutricionais</span>
              </v-tooltip>
            </v-btn>
          </v-row>
        </div>
      </v-snackbar>
    </template>
  </div>
</template>

<script>
import firebase from "firebase";
import {
  mdiCalendar,
  mdiContentSave,
  mdiPrinter,
  mdiEye,
  mdiEmailSendOutline,
  mdiDownload,
  mdiMagnify,
  mdiDotsHorizontal,
  mdiInformationOutline,
  mdiClose,
  mdiAutorenew,
  mdiTrashCanOutline,
} from "@mdi/js";

// const docx = require("docx");
// import { saveAs } from "file-saver";

import jspdf from "jspdf";
import "jspdf-autotable";
// import imagesloaded from 'imagesloaded'

// components imports
import DietaFormEdit from "../components/home/dieta/DietaFormEdit";

export default {
  components: { DietaFormEdit },
  data: function () {
    return {
      dietasSalvas: [],
      panel0: [0],
      snack: true,
      table: false,
      dialog: false,
      nomeDieta: "",
      mandatory: [
        (v) => !!v || "Campo obrigatrio",
        // v => v.length <= 10 || "Name must be less than 10 characters"
      ],
      dialogVisualizar: false,
      dialogDeletar: false,
      dialogSalvar: false,
      salvarDietaEmPaciente: false,
      salvarDietaEmSalvas: true,
      panel: [],
      panelReceitas: [],
      panelSubs: [],
      snackbar: true,
      allNutrients: [
        { codigo: "KCAL", nome: "Calorias" }, //NaN
        { codigo: "PTN", nome: "Proteina" },
        { codigo: "LIP", nome: "Lipideos" },
        { codigo: "COL", nome: "Colesterol" },
        { codigo: "CHO", nome: "Carboidrato" },
        { codigo: "FIB", nome: "Fibra alimentar" },
        { codigo: "fibra_soluvel", nome: "Fibra solvel" },
        { codigo: "fibra_insoluvel", nome: "Fibra insolvel" },
        { codigo: "cinzas", nome: "Cinzas" },
        { codigo: "calcio", nome: "Clcio" },
        { codigo: "magnesio", nome: "Magnsio" },
        { codigo: "manganes", nome: "Mangans" },
        { codigo: "fosforo", nome: "Fsforo" },
        { codigo: "ferro", nome: "Ferro" },
        { codigo: "SOD", nome: "Sdio" },
        { codigo: "potassio", nome: "Potassio" },
        { codigo: "cobre", nome: "Cobre" },
        { codigo: "zinco", nome: "Zinco" },
        { codigo: "retinol", nome: "Retinol" },
        { codigo: "re", nome: "RE" },
        { codigo: "era", nome: "ERA" },
        { codigo: "iodo", nome: "Iodo" },
        { codigo: "selenio", nome: "Selnio" },
        { codigo: "acido_folico", nome: "cido flico" },
        { codigo: "acido_pantotenico", nome: "cido pantotnico" },
        { codigo: "tiamina", nome: "Tiamina" },
        { codigo: "riboflavina", nome: "Riboflavina" },
        { codigo: "piridoxina", nome: "Piridoxina" },
        { codigo: "niacina", nome: "Niacina" },
        { codigo: "vitamina_c", nome: "Vitamina C" },
        { codigo: "vitamina_d", nome: "Vitamina D" },
        { codigo: "vitamina_e", nome: "Vitamina E" },
        { codigo: "vitamina_b12", nome: "Vitamina B12" },
        { codigo: "saturados", nome: "Saturados" },
        { codigo: "monoinsaturados", nome: "Mono-insaturados" },
        { codigo: "poliinsaturados", nome: "Poli-insaturados" },
        { codigo: "gordura_trans", nome: "Gordura trans" },
        { codigo: "triptofano", nome: "Triptofano" },
        { codigo: "treonina", nome: "Treonina" },
        { codigo: "isoleucina", nome: "Isoleucina" },
        { codigo: "leucina", nome: "Leucina" },
        { codigo: "lisina", nome: "Lisina" },
        { codigo: "metionina", nome: "Metionina" },
        { codigo: "cistina", nome: "Cistina" },
        { codigo: "fenilalanina", nome: "Fenilalanina" },
        { codigo: "tirosina", nome: "Tirosina" },
        { codigo: "valina", nome: "Valina" },
        { codigo: "arginina", nome: "Arginina" },
        { codigo: "histidina", nome: "Histidina" },
        { codigo: "alanina", nome: "Alanina" },
        { codigo: "acido_aspartico", nome: "cido aspartico" },
        { codigo: "acido_glutamico", nome: "cido glutmico" },
        { codigo: "glicina", nome: "Glcina" },
        { codigo: "prolina", nome: "Prolina" },
        { codigo: "serina", nome: "Serina" },
        { codigo: "umidade", nome: "Umidade" },
      ],
      pacienteDocID: "",
      pacienteChosen: { data: { nome: "" } },
      icons: {
        mdiCalendar,
        mdiContentSave,
        mdiPrinter,
        mdiEye,
        mdiEmailSendOutline,
        mdiDownload,
        mdiMagnify,
        mdiDotsHorizontal,
        mdiInformationOutline,
        mdiClose,
        mdiAutorenew,
        mdiTrashCanOutline,
      },
      preloadLogo: new Image(),
      preloadSignature: new Image(),
    };
  },
  computed: {
    pacientes() {
      return this.$store.state.firestore.pacientes;
    },
    dietaStatus() {
      return this.$store.state.firestore.dietaStatus;
    },
    dietaRefeicoes() {
      return this.$store.state.firestore.dietaRefeicoes;
    },
    dietaDocId() {
      return this.$store.state.firestore.dietaDocId;
    },
    dietaTitulo() {
      return this.$store.state.firestore.dietaTitulo;
    },
    alimentos() {
      return this.$store.state.firestore.alimentos;
    },
    // dietasSalvas() {
    //   return this.$store.state.firestore.dietasSalvas;
    // }
  },
  methods: {
    porcao(nr) {
      if (nr > 1) {
        return "pores";
      } else {
        return "poro";
      }
    },
    log(arg) {
      console.log(arg);
    },
    stringify(min) {
