<template>
        <v-card elevation="5">
      <v-card-title class="text-h6"> Alimentos </v-card-title>
      <v-card-text>
        <v-card
          v-for="(alimento, alimentoIndex) in alimentos"
          :key="alimentoIndex"
          outlined
        >
          <v-row class="d-flex justify-center">
            <v-col cols="12" class="d-flex justify-space-between">
              <v-row class="text-subtitle-1 ml-2 d-flex align-center">
                {{ alimento.text }}, {{ alimento.data.medida_caseira_padrao }}
              </v-row>

              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="green"
                    v-bind="attrs"
                    v-on="on"
                    class="mr-2 mx-2 black--text"
                    icon
                    @click="show(alimento)"
                  >
                    <v-icon color="green">{{ icons.mdiEye }}</v-icon>
                  </v-btn>
                </template>
                <span>mostrar alimento</span>
              </v-tooltip>

              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="red"
                    v-bind="attrs"
                    v-on="on"
                    class="mr-2 mx-2 black--text"
                    icon
                    @click="deleteAlimento(alimento.value)"
                  >
                    <v-icon color="red">{{ icons.mdiTrashCanOutline }}</v-icon>
                  </v-btn>
                </template>
                <span>Deletar Alimento</span>
              </v-tooltip>
            </v-col>
          </v-row>
        </v-card>
      </v-card-text>
    </v-card>
</template>

<script>
// import router from "../router";
import { mdiPlaylistPlus, mdiTrashCanOutline, mdiEye } from "@mdi/js";
export default {
  data: function () {
    return {
      normal: true, 
      icons: {
        mdiPlaylistPlus,
        mdiTrashCanOutline,
        mdiEye
      },
      receitasList: [],
      receita: {
        id: "",
      },
    };
  },
  computed: {
    alimentos() {
      var alphabet = function (objs) {
        function compare(a, b) {
          if (a.data.titulo < b.data.titulo) {
            return -1;
          }
          if (a.data.titulo > b.data.titulo) {
            return 1;
          }
          return 0;
        }

        return objs.sort(compare);
      };

      return alphabet(this.$store.state.firestore.alimentos);
    },
  },
  methods: {
    deleteAlimento(id) {
      console.log(id);
      this.$store.dispatch("firestore/alimentoDelete", {
        alimentoId: id,
      });
    },
    show(receita) {
      console.log(receita)
    },
    // editarReceita(id) {
    //   var receita = this.receitas.find((item) => item.value == id);
    //   if (receita !== undefined) {
    //     this.$store.dispatch("firestore/receitaId", {
    //       receitaId: id,
    //     });
    //     this.$store.dispatch("firestore/receitaUpdate", {
    //       receita: receita.data,
    //     });
    //     router.replace("/EditarReceita");
    //   }
    // },
    // numberOfReceitas() {
    //   console.log(this.receitas.length);
    // },
  },
  mounted() {
    if (this.$store.state.firestore.alimentos.length === 0) {
      this.$store.dispatch("firestore/getAlimentos");
    }
  },      
}
</script>