<template>
  <div>
    <v-dialog v-model="verEvolucaoDialog" max-width="80%" height="80%" multiple>
      <v-card>
        <v-stepper non-linear>
          <v-stepper-header>
            <v-stepper-step editable step="1">
              Peso e Composio corporal
            </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step editable step="2"> Medidas </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step editable step="3"> Sangue </v-stepper-step>
          </v-stepper-header>

          <v-stepper-items>
            <v-stepper-content step="1">
              <div class="small">
                <line-chart :chart-data="datacollectionCompos"></line-chart>
              </div>
              <v-row class="d-flex justify-center mx-4">
                <div v-for="element in checkCompos" :key="element.nome">
                  <v-checkbox
                    class="pr-4"
                    v-model="element.show"
                    :label="element.label"
                    :color="element.color"
                    @click="fillDataCompos"
                  ></v-checkbox>
                </div>
              </v-row>
            </v-stepper-content>
            <v-stepper-content step="2">
              <div class="small">
                <line-chart :chart-data="datacollectionMedidas"></line-chart>
              </div>
              <v-row class="d-flex justify-center mx-4">
                <div v-for="element in checkMedidas" :key="element.nome">
                  <v-checkbox
                    class="pr-4"
                    v-model="element.show"
                    :label="element.label"
                    :color="element.color"
                    @click="fillDataMedidas"
                  ></v-checkbox>
                </div>
              </v-row>
            </v-stepper-content>
            <v-stepper-content step="3">
              <div class="small">
                <line-chart :chart-data="datacollectionSangue"></line-chart>
              </div>
              <v-row class="d-flex justify-center mx-4">
                <div v-for="element in checkSangue" :key="element.nome">
                  <v-checkbox
                    class="pr-4"
                    v-model="element.show"
                    :label="element.label"
                    :color="element.color"
                    @click="fillDataMedidas"
                  ></v-checkbox>
                </div>
              </v-row>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showQuestionario" max-width="80%" height="80%" multiple>
      <v-card>
        <v-card-text>
          <v-form ref="form" v-model="valid" class="grey lighten-3">
            <v-container>
              <v-row class="d-flex justify-center">
                <v-col cols="12" md="4">
                  <v-text-field
                    solo
                    :prepend-icon="icons.mdiMapMarker"
                    v-model="pacienteInfos.endereco"
                    label="Endereo"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    solo
                    :prepend-icon="icons.mdiCity"
                    v-model="pacienteInfos.cidade"
                    label="Cidade"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    solo
                    :prepend-icon="icons.mdiFlagVariantOutline"
                    v-model="pacienteInfos.estado"
                    label="Estado / Pas"
