                  </p>
                  <v-text-field solo v-model="pacienteInfos.sangue" label="...">
                  </v-text-field>
                </v-container>
              </v-row>

              <v-row class="d-flex justify-center">
                <v-container fluid>
                  <p>
                    Voc fez algum
                    <strong>exame de imagem recentemente</strong>? Foi
                    encontrado algo fora da normalidade no laudo? Comente.
                  </p>
