                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.alergia"
                    value="Leite de vaca"
                    label="Leite de vaca"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.alergia"
