                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.alergia"
                    value="Ovos"
                    label="Ovos"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.alergia"
                    value="Abacaxi"
                    label="Abacaxi"
                    color="#c5d13f"
