                        color="#614021"
                      ></v-radio>
                    </v-radio-group>
                  </v-container>
                </v-col>
              </v-row>
              <v-divider></v-divider>
              <v-row class="d-flex justify-center">
                <!-- <v-col cols="1" class="d-flex justify-center align-center">
            <v-icon>{{
              icons.mdiWeightLifter
            }}</v-icon>
          </v-col> -->
                <v-col cols="12" md="3">
                  <v-container fluid>
                    <p>Faz atividade fsica?</p>
                    <v-radio-group v-model="pacienteInfos.fazAtividade" column>
                      <v-radio
                        label="Sim, fao"
                        value="Sim"
                        color="#c5d13f"
                      ></v-radio>
                      <v-radio
                        label="No, no fao"
                        value="No"
                        color="#614021"
                      ></v-radio>
                    </v-radio-group>
                  </v-container>
                </v-col>
                <v-col cols="12" md="9">
