                    <p>
                      Quais <strong>dias e horrios</strong> voc faz ou planeja
                      fazer <strong>atividade fsica</strong>? <br />Escreva em
                      detalhes para que possamos ajustar o cardpio de acordo
                      com o tipo e horrio do treino.
                    </p>
                    <v-textarea
                      solo
                      v-model="pacienteInfos.atividadeDetalhe"
                      label="..."
