                  <v-text-field solo v-model="pacienteInfos.cafe" label="...">
                  </v-text-field>
                </v-container>
              </v-row>
              <v-row class="d-flex justify-center">
                <v-container fluid>
                  <p>Gosta de comer frutas no caf da manh? Quais?</p>
                  <v-text-field solo v-model="pacienteInfos.cafe" label="...">
