                    label="Po Integral"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.cafeTolerar"
                    value="Tapioquinha com ovo; queijo ou coco"
                    label="Tapioquinha com ovo; queijo ou coco"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.cafeTolerar"
                    value="Torrada integral"
