                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.cafeTolerar"
                    value="Preparaes como panquecas proteicas"
                    label="Preparaes como panquecas proteicas"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.cafeTolerar"
                    value="Wrap integral"
                    label="Wrap integral"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.cafeTolerar"
                    value="Batata doce"
                    label="Batata doce"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
