                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.lanches"
                    value="Iogurte natural"
                    label="Iogurte natural"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.lanches"
                    value="Sementes como chia / linhaa"
                    label="Sementes como chia / linhaa"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.lanches"
                    value="Po integral"
                    label="Po integral"
                    color="#c5d13f"
                  ></v-checkbox>
                  <v-checkbox
                    hide-details
                    v-model="pacienteInfos.lanches"
