                      class="mr-2"
                      outlined
                      v-bind="attrs"
                      v-on="on"
                      @click="baixarTimelineItem(item)"
                    >
                      <v-icon>{{ icons.mdiDownload }}</v-icon>
                    </v-btn> </template
                  ><span>Baixar</span></v-tooltip
                >
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      v-show="item.type === 'atendimento'"
