    <v-dialog v-model="showFotosAll" max-width="800" multiple>
      <v-card elevation="5">
        <v-card-title class="text-h6"> Fotos </v-card-title>
        <v-card-text>
          <v-card
            v-for="(atendimento, atendimentoIndex) in atendimentos"
