              </v-col>
            </v-row>
          </v-card>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog v-model="showVerHistOpt" max-width="800" multiple>
      <v-card>
        <v-card-title class="d-flex justify-center my-2"
          >O qu voc gostaria de ver?</v-card-title
        >
        <v-divider></v-divider>
        <v-card-subtitle class="d-flex justify-center mt-3"
          >Prescries:</v-card-subtitle
        >
        <v-card-text class="d-flex justify-center">
          <v-btn color="#614021" class="mr-1" text @click="visDietasAll">
            <v-icon left color="#c5d13f"> mdi-food-apple </v-icon>
            Dietas
          </v-btn>
          <v-btn color="#614021" class="mr-1" text>
            <v-icon left color="#c5d13f"> mdi-text-box </v-icon> Formulas
          </v-btn>
          <v-btn color="#614021" class="mr-1" text>
            <v-icon left color="#c5d13f"> mdi-needle </v-icon> Exames
          </v-btn>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-subtitle class="d-flex justify-center mt-3"
          >Dados:</v-card-subtitle
        >
        <v-card-text class="d-flex justify-center">
          <v-btn color="#614021" class="mr-1" text @click="visEvolucao">
            <v-icon left color="#c5d13f"> mdi-finance </v-icon> Evoluo
          </v-btn>
          <v-btn color="#614021" class="mr-1" text @click="visBioimpAll">
            <v-icon left color="#c5d13f"> mdi-human </v-icon> Bioimpedncias
          </v-btn>
          <v-btn color="#614021" class="mr-1" text @click="visFotosAll">
            <v-icon left color="#c5d13f"> mdi-camera </v-icon> Fotos
          </v-btn>
          <v-btn color="#614021" class="mr-1" text @click="visQuestionario">
            <v-icon left color="#c5d13f"> mdi-account-question </v-icon>
            Questionrio
          </v-btn>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-subtitle class="d-flex justify-center mt-3"
          >Mais:</v-card-subtitle
        >
        <v-card-text class="d-flex justify-center">
          <v-btn color="#614021" class="mr-1" text @click="visTimeline">
            <v-icon left color="#c5d13f"> mdi-timeline-text </v-icon> Timeline
          </v-btn>
          <v-btn color="#614021" class="mr-1" text>
            <v-icon left color="#c5d13f"> mdi-calendar </v-icon> Agenda
          </v-btn>
          <v-btn color="#614021" class="mr-1" text>
            <v-icon left color="#c5d13f"> mdi-cart-variant </v-icon> Compras
          </v-btn>
